const refs = {
    galleryList: document.querySelector('.js-gallery'),
    modal: document.querySelector('.js-lightbox'),
    lightboxImage: document.querySelector('.lightbox__image'),
    modalCloseBtn: document.querySelector('[data-action="close-lightbox"]'),
    modalCloseOverlay: document.querySelector('.lightbox__overlay'),

}

const galleryMarkup = createGalleryCardsMarkup(galleryItems);
// Ð’ÐµÑˆÐ°ÐµÐ¼ ÑÐ¾Ð·Ð´Ð°Ð½Ð½ÑƒÑŽ Ð´Ð¸Ð½Ð°Ð¼Ð¸Ñ‡ÐµÑÐº Ñ€Ð°Ð·Ð¼ÐµÑ‚ÐºÑƒ Ð½Ð° ÑÑƒÑ‰ÐµÑÑ‚Ð²ÑƒÑŽÑ‰Ð¸Ð¹ Ñ”Ð»ÐµÐ¼ÐµÐ½Ñ‚
refs.galleryList.insertAdjacentHTML('beforeend', galleryMarkup);
// Ð¤ÑƒÐ½ÐºÑ†Ð¸Ñ Ñ€ÐµÐ½Ð´ÐµÑ€Ð¸Ð½Ð³Ð° Ñ€Ð°Ð·Ð¼ÐµÑ‚ÐºÐ¸ 
function createGalleryCardsMarkup(items) {
    return items
        .map(({ preview, original, description }) => {
            return `
    
    <li class="gallery__item">
        <a
          class="gallery__link"
          href="${original}"
        >
          <img
            class="gallery__image"
            src="${preview}"
            data-source="${original}"
            alt="${description}"
          />
        </a>
      </li>
    `;
        }
        )
        .join('');
}

// Ð’ÐµÑˆÐ°ÐµÐ¼ ÑÐ»ÑƒÑˆÐ°Ñ‚ÐµÐ»Ñ ÑÐ¾Ð±Ñ‹Ñ‚Ð¸Ñ Ð½Ð° ÑÐ¿Ð¸ÑÐ¾Ðº
refs.galleryList.addEventListener('click', onGalleryListClick)
function onGalleryListClick(evt) {
    // Ð¤Ð¸Ð»ÑŒÑ‚Ñ€ Ñ†ÐµÐ»Ð¸ ÐºÐ»Ð¸ÐºÐ°
    const isGalleryImageEl = evt.target.classList.contains('gallery__image');
    if (!isGalleryImageEl) {
        return;
    }
    evt.preventDefault();

    // Ð’ÐµÑˆÐ°ÐµÐ¼ ÑÐ»ÑƒÑˆÐ°Ñ‚ÐµÐ»Ñ ÑÐ¾Ð±Ñ‹Ñ‚Ð¸Ñ Ð½Ð° Ð¼Ð¾Ð´Ð°Ð»ÑŒÐ½Ð¾Ðµ Ð¾ÐºÐ½Ð¾
    refs.modal.classList.add('is-open');
    refs.lightboxImage.src = evt.target.dataset.source;
    refs.lightboxImage.alt = evt.target.alt;
    window.addEventListener('keydown', (e) => {
        if (e.key === 'Escape') {
            modalClose();
        }
    });

}

// Ð·Ð°ÐºÑ€Ñ‹Ñ‚Ð¸Ðµ Ð¼Ð¾Ð´Ð°Ð»ÑŒÐ½Ð¾Ð³Ð¾ Ð¾ÐºÐ½Ð° ÐºÐ½Ð¾Ð¿ÐºÐ¾Ð¹
refs.modalCloseOverlay.addEventListener('click', modalClose);
refs.modalCloseBtn.addEventListener('click', modalClose);
function modalClose(evt) {
    refs.modal.classList.remove('is-open');
    refs.lightboxImage.src = "";
    refs.lightboxImage.alt = "";
    window.removeEventListener('keydown', (e) => {
        if (e.key === 'Escape') {
            modalClose();
        }
    });
}
